import Vue from 'vue'
import Router from 'vue-router' //надо сначала подрубить npm i vue-router
import Home from '@/views/Home'

Vue.use(Router) //регистрируем, как компонент

export default new Router({
    mode: 'history', //чтобы были слэши в путях
    routes: [  //каждый объект - новая страница
        {
            path: '/',
            component: Home
        },
        {
            path: '/weather',
            component: () => import('./views/Weather.vue')  //Lazy-loading
        }

    ]
})